<template>
  <div>
    <div style="height: 100px;background: #FFFFFF;">
      <div style="box-sizing: border-box;padding: 12px;position: relative;" class="clearfix">
        <div style="float: left;width: 200px;top:18px;position: relative;">
          <el-input placeholder="" prefix-icon="el-icon-search" v-model="search"></el-input>
        </div>
        <div style="float: left;width: 200px;top:18px;position: relative;margin-left: 20px;">
          <el-button type="primary" @click="getShopByName()"></el-button>
        </div>
      </div>
    </div>
    <div>
      <el-table
          :data="tableData"
          style="width: 100%;"
          height="703"
          border
          >
          <el-table-column type="expand" fixed >
               <template slot-scope="props">
                 <el-form label-position="left" inline class="demo-table-expand">
                   <el-form-item label="ID" >
                     <span>{{ props.row.shopId }}</span>
                   </el-form-item>
                   <el-form-item label="" >
                     <span>{{ props.row.shopName }}</span>
                   </el-form-item>
                   <el-form-item label="" >
                     <span>{{ props.row.shopNum }}</span>
                   </el-form-item>
                   <el-form-item label="" >
                     <span>{{ props.row.shopPrice }}</span>
                   </el-form-item>
                   <el-form-item label="" >
                     <span>{{ props.row.shopInfo }}</span>
                   </el-form-item>
                   <el-form-item label="">
                     <span>{{ props.row.stime }}</span>
                   </el-form-item>
                   <el-form-item label="">
                     <span>{{ props.row.type }}</span>
                   </el-form-item>
                   <el-form-item label="" >
                     <span>{{ props.row.shopStatus }}</span>
                   </el-form-item>
                 </el-form>
               </template>
             </el-table-column>
          <el-table-column
            fixed
            prop="shopId"
            label="ID"
            width="120"
            sortable>
          </el-table-column>
          <el-table-column
            fixed
            prop="shopName"
            label=""
            width="120"
            sortable>
            <template slot-scope="scope">
              <el-input type="text" v-model="scope.row.shopName"></el-input>
            </template>
          </el-table-column>
          <el-table-column
            prop="shopNum"
            label=""
            width="120">
            <template slot-scope="scope">
              <el-input type="text" v-model="scope.row.shopNum"></el-input>
            </template>
          </el-table-column>
          <el-table-column
            prop="shopPrice"
            label=""
            width="120">
            <template slot-scope="scope">
              <el-input v-model="scope.row.shopPrice"></el-input>
            </template>
          </el-table-column>
          <el-table-column
            prop="shopInfo"
            label=""
            >
            <template slot-scope="scope">
              <el-input v-model="scope.row.shopInfo"></el-input>
            </template>
          </el-table-column>
          <el-table-column
            prop="stime"
            label=""
            width="120"
            sortable>

          </el-table-column>
          <el-table-column
            prop="type"
            label=""
            width="120"
          >
          <template slot-scope="scope">

          <el-select v-model="scope.row.type"  placeholder="">
             <el-option
               v-for="item in options"
               :key="item.value"
               :label="item.label"
               :value="item.value">
             </el-option>
           </el-select>
          </template>
          </el-table-column>
          <el-table-column
            prop="shopStatus"
            label=""
            width="80">
            <template slot-scope="scope">
             <el-select v-model="scope.row.shopStatus"  placeholder="">
                <el-option
                  v-for="item in status"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value">
                </el-option>
              </el-select>
            </template>
            </el-table-column>
            <el-table-column
              prop="shopStatus"
              label=""
              width="80">
              <template slot-scope="scope">
                <el-button  type="primary" @click="updateShop(scope.$index,tableData)"></el-button>
              </template>
              </el-table-column>
        </el-table>
    </div>

  </div>
</template>

<script>
  export default{
    data(){
      return{
        tableData:[
         //{shopId:'',shopName:'dad',shopNum:0,shopUrl:'',shopPrice:0,shopInfo:'',stime:'',type:'',shopStatus:0}
        ],
        search:'',
        table:[],
        options:[
          //
          {
            value:'',
            label:''
          },{
            value:'',
            label:''
          },
          {
            value:'',
            label:''
          },

          {
            value:'',
            label:''
          },
          {
            value:'',
            label:''
          },
          {
            value:'',
            label:''
          },
          {
            value:'',
            label:''
          },
          {
            value:'',
            label:''
          },
          {
            value:'',
            label:''
          },
          {
            value:'',
            label:''
          },
          {
            value:'',
            label:''
          },
          {
            value:'',
            label:''
          },
          {
            value:'',
            label:''
          },{
            value:'',
            label:''
          }
        ],
        status:[
          {
            value:'',
            label:''
          },
          {
            value:'',
            label:''
            }
        ]
      }
    },
    mounted:function(){

      if(sessionStorage.getItem("userinfo")!=null){
        this.getShopTable()
      }else{
        this.$message({
          type:'warning',
          message:''
        })
      }


    },
    methods:{

      islogin(){
        if(sessionStorage.getItem("userinfo")!=null){
          return true
        }else{
          return false
        }
      },

      getShopTable(){

        this.axios({
          url:'http://localhost:8080/shop/shopdata',
          method:'post',
          responseType: 'json'
        }).then(resp=>{
          if(resp.data.status==200){
            this.tableData=resp.data.data
            this.table=resp.data.data
          }
        })
      },
      getShopByName(){
        if(this.islogin()){
          let search = this.search
          this.axios({
            url:'http://localhost:8080/shop/shopsearch',
            method:'post',
            responseType:'json',
            data:{
              search:search
            }
          }).then(resp=>{
            if(resp.data.status=200){
              if(resp.data.data!=null){
                this.tableData=resp.data.data
              }else{
                this.$message({
                   message: '',
                   type: 'warning'
                })
              }
            }
          })
        }else{
          this.$message({
            message:"",
            type:'warning'
          })
        }

      },
      updateShop(index,data){

        if(this.islogin()){
          this.axios({
            url:'http://localhost:8080/shop/updateshop',
            method:'post',
            data:data[index]
          }).then(resp=>{
            if(resp.data.status=200){
              this.$message({
                message:resp.data.message,
                type:'success'
              })
            }
          }).catch(resp=>{
                this.$message.error("")
                this.tableData=this.table
            })
        }else{
          this.$message({
            message:"",
            type:'warning'
          })
        }


      }
    }

  }
</script>

<style>
  .demo-table-expand {
     font-size: 0;
   }
   .demo-table-expand label {
     width: 90px;
     color: #99a9bf;
   }
   .demo-table-expand .el-form-item {
     margin-right: 0;
     margin-bottom: 0;
     width: 100%;
   }
</style>



