<template>
  <div class="deliverygoods">
    <div class="deliverygoods-content">
      <div style="height: 100%;overflow: scroll;width: 100%;" class="hid-scoll">
        <el-table :data="tableData" style="width: 100%">
          <el-table-column type="expand">
            <template slot-scope="props">
              <el-form label-position="left" inline class="tabledata-algin">
                <el-form-item label="">
                  <span>{{ props.row.orderTime }}</span>
                </el-form-item>
                <el-form-item label=" ID">
                  <span>{{ props.row.orderId }}</span>
                </el-form-item>
                <!--                <el-form-item label=" ID">
                  <span>{{ props.row.userId }}</span>
                </el-form-item>
                <el-form-item label=" ID">
                  <span>{{ props.row.shopId }}</span>
                </el-form-item> -->
                <el-form-item label="">
                  <span>{{ props.row.shopNum }}</span>
                </el-form-item>
                <el-form-item label="">
                  <span>{{ props.row.userName }}</span>
                </el-form-item>
                <el-form-item label="">
                  <span>{{ props.row.userTel }}</span>
                </el-form-item>
                <el-form-item label="">
                  <span>{{ props.row.address }}</span>
                </el-form-item>
                <el-form-item label="">
                  <span>{{ props.row.total }}</span>
                </el-form-item>
                <el-form-item label="">
                  <span>{{ props.row.orderStatus }}</span>
                </el-form-item>
              </el-form>
            </template>
          </el-table-column>
          <el-table-column sortable label="" prop="orderTime">
          </el-table-column>
          <el-table-column label=" ID" prop="orderId">
          </el-table-column>
          <!-- <el-table-column
            label=" ID"
            prop="userId">
          </el-table-column>
          <el-table-column
            label=" ID"
            prop="shopId">
          </el-table-column> -->
          <el-table-column label="" prop="shopNum">
          </el-table-column>

          <el-table-column label="" prop="userName">
          </el-table-column>
          <el-table-column label="" prop="userTel">
          </el-table-column>
          <el-table-column label="" prop="address">
          </el-table-column>
          <el-table-column label="" prop="total">
          </el-table-column>
          <el-table-column label="" prop="orderStatus">
          </el-table-column>
          <el-table-column label="" width="210px">
            <template slot-scope="scope">
              <el-button type="primary" @click="sendGoods(scope.row.orderId,scope.$index)"></el-button>
              <el-button type="success" @click="noGoods(scope.row.orderId,scope.$index)"></el-button>
            </template>
          </el-table-column>

        </el-table>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    mounted: function() {
      this.getOrderByType()
    },
    data() {
      return {
        tableData: [

        ]
      }
    },
    methods: {
      isLogin() {
        if (sessionStorage.getItem("userinfo")) {
          return true
        } else {
          return false
        }
      },
      getOrderByType() {
        if (this.isLogin()) {
          this.axios({
            url: 'http://localhost:8080/userorder/manserchstatus',
            method: 'post',
            params: {
              orderstatus: 1
            }
          }).then(resp => {
            if (resp.data.status == 200) {
              this.tableData = resp.data.data
            }
          }).catch(resp => {
            this.$message.error("")
          })

        } else {
          this.$message({
            type: 'warning',
            message: ''
          })
        }
      },
      sendGoods(id, index) {
        this.axios({
          url: 'http://localhost:8080/userorder/mandeliverygoods',
          method: 'post',
          params: {
            orderid: id
          }
        }).then(resp => {
          if (resp.data.status == 200) {
            this.$message({
              type: 'success',
              message: ''
            })
            this.tableData.splice(index, 1)
          }
        }).catch(resp => {
          this.$message.error("")
        })
      },
      noGoods(id, index) {
        this.axios({
          url: 'http://localhost:8080/userorder/updatestatus',
          method: 'post',
          params: {
            orderid: id
          }
        }).then(resp => {
          if (resp.data.status == 200) {
            this.$message({
              type: 'success',
              message: ''
            })
            this.tableData.splice(index, 1)
          }
        }).catch(resp => {
          this.$message.error("")
        })
      },
    }
  }
</script>

<style>
  .deliverygoods {
    width: 100%;
    height: 100%;
    position: relative;
    background-color: #FFFFFF;
  }

  .deliverygoods-content {
    width: 100%;
    height: 100%;
    box-sizing: border-box;
    padding: 20px;
  }

  .hid-scoll::-webkit-scrollbar {
    display: none;
  }

  .tabledata-algin {
    font-size: 0;
  }

  .tabledata-algin label {
    width: 300px;
  }

  .tabledata-algin .el-form-item {
    margin-right: 0;
    margin-bottom: 0;
    width: 100%;
  }
</style>



